<template>
  <div class="login">
    <!-- 头像 -->
    <div class="headele">
      <img src="../assets/111.jpg" alt />
    </div>
    <van-tabs line-width="50px">
      <van-tab title="账号登录" class="accountLogin">
        <van-form @submit="doLogin">
          <van-field
            v-model="username"
            name="account"
            label="账号"
            placeholder="请输入账号"
            :rules="[{ required: true, message: '请填写账号' }]"
          />
          <van-field
            v-model="password"
            type="password"
            name="password"
            label="密码"
            placeholder="请输入密码"
            autocomplete
            :rules="[{ required: true, message: '请填写密码' }]"
          ></van-field>
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">登录</van-button>
          </div>
        </van-form>
      </van-tab>

      <!-- 快速登录 -->
      <van-tab title="快速登录" class="speedinessLogin">
        <van-form @submit="phoneLogin">
          <van-field
            v-model="phone"
            name="phone"
            label="手机号"
            placeholder="请输入手机号"
            :rules="[{ required: true, message: '请填写正确的手机号',pattern:/^1[345678]\d{9}$/}]"
          />
          <van-field
            v-model="captcha"
            type="text"
            name="captcha"
            label="验证码"
            placeholder="输入验证码"
            :rules="[{ required: true, message: '请填写验证码' }]"
          >
            <template #button>
              <van-button
                size="small"
                type="warning"
                native-type="button"
                :disabled="secen!=60"
                @click="sendcaptcha"
              >{{secen &lt; 60?`${secen}秒后重新获取`:"获取验证码"}}</van-button>
            </template>
          </van-field>
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">提交</van-button>
          </div>
        </van-form>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import { mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      captcha: "",
      username: "",
      password: "",
      phone: "",
      key: "",
      setOut: "",
      secen: 60,
    };
  },
  created() {
    this.$axios.get("/api/verify_code").then((data) => {
      if (data.data.status == 200 && data.data.data.key) {
        this.key = data.data.data.key;
      }
    });
  },
  methods: {
    sendcaptcha() {
      this.$axios
        .post("api/register/verify", {
          key: this.key,
          phone: this.phone,
          type: "login",
        })
        .then((d) => {
          this.$toast(d.data.msg);
          if (d.data.status == 200) {
            this.setOut = setInterval(() => {
              if (this.secen <= 0) {
                clearTimeout(this.setOut);
                this.secen = 60;
                return;
              }
              this.secen--;
            }, 1000);
          }
        });
    },
    phoneLogin(value) {
      this.$axios.post("/api/login/mobile", value).then((d) => {
        if (d.data.status == 200) {
          this.login(d.data.data.token);
          this.getUserLogin(d.data.data.token);
          this.$toast(d.data.msg);

          setTimeout(() => {
            this.$router.push("/my");
          }, 1000);
        } else {
          this.$toast(d.data.msg);
        }
      });
    },
    doLogin(value) {
      this.$axios.post("api/login", value).then((d) => {
        if (d.data.status == 200) {
          this.login(d.data.data.token);
          this.getUserLogin(d.data.data.token);
          this.$toast(d.data.msg);

          setTimeout(() => {
            this.$router.push("/my");
          }, 1000);
        } else {
          this.$toast(d.data.msg);
        }
      });
    },
    ...mapMutations(["login"]),
    ...mapActions(["getUserLogin"]),
  },
};
</script>
<style lang="less" scoped>
.login {
  height: 100vh;
  background-image: linear-gradient(180deg, #eb5447 0, #ff8e3b);

  .headele {
    display: flex;
    width: 100vw;
    height: 180px;
    justify-content: center;
    align-items: center;
    padding-top: 25px;

    img {
      border: 2px solid #fff;
      width: 100px;
      height: 100px;
      border-radius: 50%;
    }
  }
}
.van-tabs {
  width: 80vw;
  margin: 0px auto;
  height: 250px;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
}
</style>
